body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.homepageImage { 
    margin: 0px 30px 15px 30px;
    padding: 0px;
    border: 2px solid rgb(255, 0, 200);
    max-width: 625px;
    width: 80%;
    border-radius: 40px;
}

.homepageHeader {
    margin: 30px 30px 15px 30px;
    font-size: 10px;
    letter-spacing: 8px;
    color: blueviolet;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    /* font-variant: small-caps; */
    /* font-style: italic; */
  }

.blogPost { 
    text-align: left;
    margin: 30px 30px 30px 30px;
    padding-left: 50px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 50px;
    font-family: Arial, Helvetica, sans-serif, sans-serif;
    background-color: rgb(1, 20, 20);
    letter-spacing: 6px;

}

.blogPost .title { 
    font-size: 30px;
    font-style: italic;
}

.blogPost .date { 
    font-size: 15px;
    font-style: italic;
}

.blogPost .post { 
    padding-top: 5px;
    letter-spacing: 3px;
    font-size: 12px;
}
.projectList {
    text-align: left;
    margin: 30px 30px 30px 30px;
    padding-top: 30px;
    padding-bottom: 30px;
    padding-left: 50px;
    padding-right: 50px;
    font-family: Arial, Helvetica, sans-serif;
    background-color: rgb(1, 20, 20);
    list-style-type: none;
}

.projectList .projectName {
    text-align: left;
    font-size: 15px;
    padding: 0px;
    text-decoration: none;
    letter-spacing: 5px;
    font-weight: 100;
}

.projectLink {
    text-align: left;
    font-size: 10px;
    padding: 0px;
    text-decoration: none;
    letter-spacing: 3px;
    font-weight: 100;
}

.container {
    border-radius: 50%;
    background-color: rgb(1, 20, 20);
    padding: 20px;
    margin: 30px 30px 30px 30px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    -webkit-font-feature-settings: "c2sc";
            font-feature-settings: "c2sc";
    font-variant: small-caps;
}

.coverArt {
    width: 30%;
    min-width: 350px;
    border: 3px solid rgb(255, 0, 200);
    border-radius: 40px;
}

.albumTitle{
    font-size: 35px;
    background-color: rgb(0, 0, 0);
    padding-top: 15px;
    padding-bottom: 1px;
    /* font-variant: small-caps; */
    letter-spacing: 10px;
}

.song {
    font-size: 13px;
    letter-spacing: 6px;

}



